<h2 id="linux系统启动过程" tabindex="-1"><a class="header-anchor" href="#linux系统启动过程" aria-hidden="true">#</a> Linux系统启动过程</h2>
<h3 id="内核的引导" tabindex="-1"><a class="header-anchor" href="#内核的引导" aria-hidden="true">#</a> 内核的引导</h3>
<p>当计算机打开电源后，首先是BIOS开机自检，按照BIOS中设置的启动设备（通常是硬盘）来启动。</p>
<p>操作系统接管硬件以后，首先读入 /boot 目录下的内核文件</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/bg2013081702.png" alt="bg2013081702"></p>
<p>运行init</p>
<p>init 进程是系统所有进程的起点，你可以把它比拟成系统所有进程的老祖宗，没有这个进程，系统中任何进程都不会启动。</p>
<p>init 程序首先是需要读取配置文件 /etc/inittab。</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/bg2013081703.png" alt="bg2013081703"></p>
<h4 id="运行级别" tabindex="-1"><a class="header-anchor" href="#运行级别" aria-hidden="true">#</a> 运行级别</h4>
<p>许多程序需要开机启动。它们在Windows叫做&quot;服务&quot;（service），在Linux就叫做&quot;守护进程&quot;（daemon）。</p>
<p>init进程的一大任务，就是去运行这些开机启动的程序。</p>
<p>但是，不同的场合需要启动不同的程序，比如用作服务器时，需要启动Apache，用作桌面就不需要。</p>
<p>Linux允许为不同的场合，分配不同的开机启动程序，这就叫做&quot;运行级别&quot;（runlevel）。也就是说，启动时根据&quot;运行级别&quot;，确定要运行哪些程序。</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/bg2013081704.png" alt="bg2013081704"></p>
<p>Linux系统有7个运行级别(runlevel)：</p>
<ul>
<li>
<p>运行级别0：系统停机状态，系统默认运行级别不能设为0，否则不能正常启动</p>
</li>
<li>
<p>运行级别1：单用户工作状态，root权限，用于系统维护，禁止远程登陆</p>
</li>
<li>
<p>运行级别2：多用户状态(没有NFS)</p>
</li>
<li>
<p>运行级别3：完全的多用户状态(有NFS)，登陆后进入控制台命令行模式</p>
</li>
<li>
<p>运行级别4：系统未使用，保留</p>
</li>
<li>
<p>运行级别5：X11控制台，登陆后进入图形GUI模式</p>
</li>
<li>
<p>运行级别6：系统正常关闭并重启，默认运行级别不能设为6，否则不能正常启动</p>
</li>
</ul>
